<template>
  <div>
    <div class="login-card" v-loading="loading">
      <!-- 登录框 -->
      <el-dialog
        title="登录框"
        :visible.sync="dialogVisible"
        width="30%"
        >
        <el-form ref="form" :model="fomrModel" label-width="80px" :rules="rules">
          <el-form-item label="用户名" prop="username">
            <el-input v-model="fomrModel.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="fomrModel.password"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer" >
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="onLogin">登 录</el-button>
        </span>
      </el-dialog>

      <!-- 登录按钮 -->
      <div class="onlogin">
        <button @click="dialogVisible = true">登录</button>
      </div>
      <!-- 影子盒子 -->
      <div class="shadow-box1"></div>
      <div class="shadow-box2"></div>
      <div class="shadow-box3">
        <img src="@/assets/MEDM2.jpg" alt="">
      </div>
      <div class="shadow-box4">
        <img src="@/assets/AFGK2 (2).jpg" alt="">
      </div>
    </div>
  </div>
</template>

<script>
import { studentLogin } from '@/api/student'
import { guserLogin } from '@/api/goods'
import { userLogin } from '@/api/login'
import { Message } from 'element-ui'
export default {
  name: 'loginIndex',
  data () {
    return {
      dialogVisible: false,
      loading: false,
      fomrModel: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', tirgger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', tirgger: 'blur' }
        ]
      }
    }
  },
  methods: {
    async onLogin () {
      this.$refs.form.validate(async (boolen) => {
        if (boolen) {
          this.loading = true
          this.dialogVisible = false
          const data = await userLogin(this.fomrModel)
          // console.log(data)
          this.$store.commit('user/setUserInfo', data.token)
          Message.success('登录成功')
          this.$router.replace('/')
          this.$destroy()
          // 登录成功后
          // 调用登录商品系统
          const res = await guserLogin()
          this.$store.commit('user/setgoodsUserInfo', res.data)
          // 调用登录学生系统
          const { data: result } = await studentLogin()
          this.$store.commit('user/setStudentToken', result.data.token)
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login-card{
  width: 100%;
  height: auto;
  margin: 0 auto;
  position: relative;
}
.onlogin{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  button{
    width: 200px;
    height: 45px;
    outline: none;
    border: none;
    font-size: 19px;
    color: rgb(0, 0, 0);
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    cursor: pointer;
    transition: all .3s;
    display: block;
    z-index: 99;
  }
  button:active{
    background-color: rgb(7, 165, 7);
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 1);
  }
}
.shadow-box1{
  padding: 2rem 4rem;
    border-radius: 10px;
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.1);
    max-width: 343px;
    width: 100%;
    height: 157px;
    position: absolute;
    top: 80px;
    left: 80px;
}
.shadow-box2{
  padding: 2rem 4rem;
    border-radius: 10px;
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.1);
    max-width: 343px;
    width: 100%;
    height: 157px;
    position: absolute;
    bottom: 200px;
    right: 80px;
}

.shadow-box3{
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.3);
    width: 460px;
    height: 460px;
    border-radius: 50%;
    position: absolute;
    bottom: 230px;
    left: 650px;
    background-color: black;
    img{
      width: 250px;
      height: 250px;
      margin-top: 80px;
      margin-left: 20px;
      border-radius: 50%;
    }
}

.shadow-box4{
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.3);
    width: 430px;
    height: 430px;
    border-radius: 50%;
    position: absolute;
    top: 70px;
    right: 200px;
    background-color: #115724;
    img{
      width: 250px;
      height: 250px;
      margin-top: 60px;
      margin-left: 20px;
      border-radius: 50%;
    }
}
</style>
