<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <div ref="echarts" style="height: 280px;"></div>
  </div>
</template>

<script>
import { getHomeDate } from '@/api/home'
import * as echarts from 'echarts'
export default {
  data () {
    return {}
  },
  async created () {
    const { data } = await getHomeDate()
    // console.log(data.userData)

    const myChart = echarts.init(this.$refs.echarts)
    const xAxis = data.userData.map(i => i.date)

    myChart.setOption({
      title: {
        text: '用户数据柱状图'
      },
      tooltip: {},
      // 注明
      legend: {
        data: ['新增用户', '活跃用户']
      },
      // x轴
      xAxis: {
        data: xAxis,
        type: 'category'
      },
      // y轴
      yAxis: [
        {
          type: 'value'
        }
      ],
      // 数据
      series: [
        {
          name: '新增用户',
          data: data.userData.map(i => i.new),
          type: 'bar'
        },
        {
          name: '活跃用户',
          data: data.userData.map(i => i.active),
          type: 'bar'
        }
      ]
    })
  }
}
</script>

<style lang="less" scoped></style>
