<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <div ref="echarts" style="height: 280px;"></div>
  </div>
</template>

<script>
import { getHomeDate } from '@/api/home'
import * as echarts from 'echarts'
export default {
  data () {
    return {}
  },
  async created () {
    const { data } = await getHomeDate()
    // console.log(data.videoData)

    const myChart = echarts.init(this.$refs.echarts)
    myChart.setOption({
      title: {
        text: '商品数据扇形图'
      },
      tooltip: {
        trigger: 'item'
      },
      series: [
        {
          data: data.videoData,
          type: 'pie'
        }
      ]
    })
  }
}
</script>

<style lang="less" scoped></style>
