<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <el-row >
      <el-col class="left-card" :span="6" style="padding-right: 10px;">
        <el-card class="box-card">
          <div class="user">
            <img :src="userinfo.user_pic || defaultPic" @click="$router.push('/personal')">
            <div class="userinfo text item">
              <p class="name">{{ userinfo.nickname || userinfo.username }}</p>
              <p class="access">超级管理员</p>
            </div>
          </div>
          <div class="login-info text item">
            <p>上次登录时间：<span>2023-10-25</span></p>
            <p>上次登录地点：<span>南宁</span></p>
          </div>
        </el-card>

        <!-- 表1 -->
        <el-card class="box-card">
          <el-table>
            <el-table-column></el-table-column>
          </el-table>
        </el-card>
        <!-- 表2 -->
        <el-card class="box-card">
          <el-table
            :data="tableData"
            style="width: 100%">
            <el-table-column v-for="(val, key) in tableLabel" :key="val"
              :prop="key"
              :label="val"
              width="auto">
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>

      <el-col :span="18" style="padding-left: 10px;">
        <!-- 今日信息 -->
        <div class="num">
          <el-card :body-style="{display: 'flex'}"
          v-for="i in countData"
          :key="i.name">
            <i class="icon"
            :class="`el-icon-${i.icon}`"
            :style="{background: i.color}"></i>
            <div class="detail">
              <p class="price">￥{{ i.value }}</p>
              <p class="desc">{{ i.name }}</p>
            </div>
          </el-card>
        </div>
        <!-- 折线图1 -->
        <el-card style="height: 280px;">
          <lineChart></lineChart>
        </el-card>

        <div class="graph">
          <!-- 柱状图 -->
          <el-card style="height: 270px;">
            <histogram></histogram>
          </el-card>
          <!-- 扇形图 -->
          <el-card style="height: 270px;">
            <fandiagram></fandiagram>
          </el-card>
        </div>

        <!-- 折线图2 -->
        <el-card style="height: 230px; margin-top: 10px;"></el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { userLogin } from '@/api/goods'
import { getUserInfo } from '@/api/userinfo'
import { getHomeDate } from '@/api/home.js'
import lineChart from '@/components/lineChart.vue'
import histogram from '@/components/histogram.vue'
import fandiagram from '@/components/fandiagram.vue'
import defaultPic from '@/assets/MEDM2.jpg'
export default {
  data () {
    return {
      userinfo: {},
      defaultPic, // 默认头像
      tableData: [],
      tableLabel: {
        name: '商品',
        todayBuy: '今日购买',
        monthBuy: '本月购买',
        totalBuy: '总购买'
      },
      countData: [
        {
          name: '今日支付订单',
          value: 1234,
          icon: 'success',
          color: '#2ec7c9'
        },
        {
          name: '今日收藏订单',
          value: 210,
          icon: 'star-on',
          color: '#ffb980'
        },
        {
          name: '今日未支付订单',
          value: 1234,
          icon: 's-goods',
          color: '#5ab1ef'
        },
        {
          name: '本月支付订单',
          value: 1234,
          icon: 'success',
          color: '#2ec7c9'
        },
        {
          name: '本月收藏订单',
          value: 210,
          icon: 'star-on',
          color: '#ffb980'
        },
        {
          name: '本月未支付订单',
          value: 1234,
          icon: 's-goods',
          color: '#5ab1ef'
        }
      ]
    }
  },
  async created () {
    const { data } = await getHomeDate()
    this.tableData = data.tableData
    // console.log(data)

    // 调用获取用户基本信息接口
    const { data: res } = await getUserInfo()
    // console.log(res)
    this.userinfo = res
  },

  mounted () {
  },

  components: {
    lineChart,
    histogram,
    fandiagram
  }
}
</script>

<style lang="less" scoped>
.left-card{
  z-index: 999;
}
.box-card{
  min-width: 400px;
  margin-bottom: 10px;
}
.user{
  display: flex;
  align-items: center;
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
  img{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-left: 40px;
    cursor: pointer;
  }
  .userinfo{
    .name{
      font-size: 32px;
      margin-bottom: 10px;
    }
    .access{
      color: #9999;
    }
  }
}

.login-info{
  p{
    line-height: 28px;
    font-size: 14px;
    color: #9999;
    span{
      color: #9999;
      margin-left: 60px;
    }
  }
}

.num{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  min-width: 1150px;
  max-width: 1350px;
  .icon{
    width: 80px;
    height: 80px;
    font-size: 30px;
    text-align: center;
    line-height: 80px;
    color: white;
  }
  .detail{
    display: flex;
    flex-direction: column; //上下
    justify-content: center;
    margin-left: 20px;
    .price{
      font-size: 30px;
      margin-bottom: 10px;
      line-height: 30px;
      height: 30px;
    }
    .desc{
      font-size: 14px;
      text-align: center;
      color: #9999;
    }
  }
  .el-card{
    width: 32%;
    margin-bottom: 20px;
  }
}
.graph{
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  .el-card{
    width: 49%;
  }
}
</style>
