<template>
  <div>
    <div ref="echarts" style="height: 280px;">

    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts'
import { getHomeDate } from '@/api/home'
export default {
  data () {
    return {

    }
  },

  async created () {
    const { data } = await getHomeDate()
    // console.log(data.orderData)
    // 初始化echarts实例
    const myChart = echarts.init(this.$refs.echarts)
    // 指定图表的配置项和数据
    const xAxis = data.orderData.date
    const picData = Object.keys(data.orderData.data[0])
    const series = []
    picData.forEach(key => {
      series.push({
        name: key,
        type: 'line', // 类型
        data: data.orderData.data.map(i => i[key])
      })
    })
    // console.log(series)
    // console.log(xAxis)
    myChart.setOption({
      // 标题
      title: {
        text: '订单详细折线图'
      },
      tooltip: {},
      // 注明
      legend: {
        data: picData
      },
      // x轴
      xAxis: {
        data: xAxis
      },
      // y轴
      yAxis: {},
      // 数据
      series: series

    })
  }
}
</script>

