// 学生管理信息
import $http from '@/utile/$student.js'
import Qs from 'qs'

// 注册
export const studentRegister = () => $http.post('/register', {
  username: 'shevvy',
  password: '123456'
})

// 登录
export const studentLogin = () => $http.post('/login', {
  username: 'shevvy',
  password: '123456'
})

// 获取学生列表
export const getStudentList = () => $http.get('/students')

// 添加学生
export const addNewStudent = (form) => $http.post('/students', {
  ...form
})

// 编辑学生
export const editStudent = (form, id) => {
  const {
    name, age, gender, hope_salary: hopeSalary, salary, group,
    province, city, area
  } = form
  return $http.put(`/students/${id}`, {
    name,
    age,
    gender,
    hope_salary: hopeSalary,
    salary,
    group,
    province,
    city,
    area
  })
}

// 删除学生
export const deleteStudent = (id) => $http.delete(`/students/${id}`)

// 获取学生可视化数据
export const showStudentData = () => $http.get('/dashboard')
