import axios from 'axios'
import store from '@/store/index'
import router from '@/router'
import { studentLogin, studentRegister } from '@/api/student'

const baseURL = 'http://ajax-api.itheima.net'

const instance = axios.create({
  baseURL,
  timeout: 20000
})

// 添加请求拦截器
instance.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  config.headers.Authorization = store.state.user.studentToken
  return config
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error)
})

// 添加响应拦截器
instance.interceptors.response.use(function (response) {
  // 2xx 范围内的状态码都会触发该函数。
  // 对响应数据做点什么
  return response
}, async function (error) {
  // 超出 2xx 范围的状态码都会触发该函数。
  // 对响应错误做点什么

  // 未注册
  if (error.response?.status === 400) {
    const data = await studentRegister()
    console.log(data)
    location.reload()
  }
  // 未登录
  if (error.response?.status === 401) {
    // router.push('/login')
    const { data: { data } } = await studentLogin()
    // console.log(data)
    store.commit('user/setStudentToken', data.token)
    // router.push('/login')
    location.reload()
  }
  return Promise.reject(error)
})

export default instance
export { baseURL }
