// vuex 持久化

const USER_INFO = 'shevvy_boy'
const GOODS_TOKEN = 'shevvy_goods'
const STUDENT_TOKEN = 'shevvy_student'

// 获取用户token
export const getUserToken = () => {
  const result = localStorage.getItem(USER_INFO)
  return result ? JSON.parse(result) : ''
}

// 储存用户token
export const setUserToken = (token) => {
  return localStorage.setItem(USER_INFO, JSON.stringify(token))
}

// 删除用户token
export const removeUserToken = () => {
  localStorage.removeItem(USER_INFO)
  localStorage.removeItem(GOODS_TOKEN)
  localStorage.removeItem(STUDENT_TOKEN)
}

// 获取用户商品对象
export const getDoodsUserToken = () => {
  const result = localStorage.getItem(GOODS_TOKEN)
  return result ? JSON.parse(result) : {}
}
// 储存用户商品对象
export const setGoodsUserToken = (obj) => {
  return localStorage.setItem(GOODS_TOKEN, JSON.stringify(obj))
}

// 获取学生数据
export const getStudentToken = () => {
  const result = localStorage.getItem(STUDENT_TOKEN)
  return result ? JSON.parse(result) : ''
}
// 储存学生数据
export const setStudentToken = (token) => {
  return localStorage.setItem(STUDENT_TOKEN, JSON.stringify(token))
}
