<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div class="tabs">
    <el-tag class="tab-items"
      v-for="(item, index) in tags"
      @click="changeMenu(item)"
      @close="handleClose(item, index)"
      :key="item.path"
      type="success"
      :closable="item.path !== '/home'"
      :effect="$route.path === item.path ? 'dark' : 'plain'">
      {{ item.label }}
    </el-tag>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {

    }
  },
  methods: {
    // 切换tag
    changeMenu (i) {
      if (i.path === this.$route.path) return
      this.$router.push(i.path)
    },
    // 关闭tag
    handleClose (i, index) {
      // 高亮时，代表为选中状态
      if (i.path === this.$route.path) {
        this.$router.replace('/')
      }
      // 删除
      this.$store.commit('tag/removeMenu', index)
    }
  },
  computed: {
    ...mapState({
      tags: state => state.tag.tabsList
    })
  }
}
</script>

<style lang="less" scoped>
.tab-items{
  margin-right: 9px;
  cursor: pointer;
}
</style>
