import {
  getUserToken,
  setUserToken,
  getDoodsUserToken,
  setGoodsUserToken,
  getStudentToken,
  setStudentToken
} from '@/utile/stores.js'

export default {
  namespaced: true,
  state: {
    token: getUserToken(),
    guserInfo: getDoodsUserToken(),
    studentToken: getStudentToken()
  },
  mutations: {
    setUserInfo (state, token) {
      state.token = token
      setUserToken(token)
    },
    setgoodsUserInfo (state, obj) {
      setGoodsUserToken(obj)
    },
    setStudentToken (state, token) {
      setStudentToken(token)
    }
  }
}
