import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store/index'

import Layout from '@/views/layout/index.vue'
import Login from '@/views/login/index.vue'
import Home from '@/views/layout/home.vue'

const GoodsMang = () => import('@/views/layout/goodsmang.vue')
const UserMang = () => import('@/views/layout/usermang.vue')
const ProductList = () => import('@/views/layout/productlist.vue')
const ProdCategory = () => import('@/views/layout/prodcategory.vue')
const OrderList = () => import('@/views/layout/orderlist.vue')
const OrderCollect = () => import('@/views/layout/odercollect.vue')
const AdvList = () => import('@/views/layout/advlist.vue')
const Articlecate = () => import('@/views/layout/articategory.vue')
const Articlemang = () => import('@/views/layout/articlemang.vue')
const Personal = () => import('@/views/layout/personal.vue')
const StudList = () => import('@/views/layout/studentlist.vue')
const StudData = () => import('@/views/layout/studentdata.vue')
const SearchMang = () => import('@/views/layout/searchmang.vue')

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      path: '/',
      component: Layout,
      redirect: '/home',
      children: [
        { path: '/home', component: Home },
        { path: '/goodsmang', component: GoodsMang },
        { path: '/usermang', component: UserMang },
        { path: '/productlist', component: ProductList },
        { path: '/prodcategory', component: ProdCategory },
        { path: '/orderlist', component: OrderList },
        { path: '/ordercollect', component: OrderCollect },
        { path: '/advlist', component: AdvList },
        { path: '/articlecate', component: Articlecate },
        { path: '/articlemang', component: Articlemang },
        { path: '/studentlist', component: StudList },
        { path: '/studentdata', component: StudData },
        { path: '/personal', component: Personal },
        { path: '/searchmang', component: SearchMang }
      ]
    },
    { path: '/login', name: 'login', component: Login }
  ]
})

router.beforeEach((to, from, next) => {
  if (!store.state.user.token && to.path !== '/login') {
    next('/login')
  }
  next(true)
})
export default router
