export default {
  namespaced: true,
  state: {
    tabsList: [
      {
        path: '/home',
        label: '首页'
      },
      {
        path: '/personal',
        label: '个人中心'
      }
    ]
  },

  mutations: {
    // 设置tag
    selecMenu (state, val) {
      if (val.path !== '/home') {
        const index = state.tabsList.findIndex(i => i.path === val.path)
        // console.log(val)

        if (index === -1) {
          state.tabsList.push(val)
        }
      }
    },
    // 删除tag
    removeMenu (state, index) {
      state.tabsList.splice(index, 1)
    }
  }
}
