// 用户信息
import $http from '@/utile/$user.js'

// 获取用户基本信息
export const getUserInfo = () => $http.get('/my/userinfo')

// 上传头像
export const uploadAvatar = (avatar) => $http.patch('/my/update/avatar', { avatar })

// 更新用户信息
export const updateUserInfo = (form) => $http.put('/my/userinfo', form)

// 修改密码
export const updateUserPswd = (form) => $http.patch('/my/updatepwd', form)
