import axios from 'axios'
import store from '@/store/index'
import { Message } from 'element-ui'
import router from '@/router'
import { studentRegister } from '@/api/student'
import { removeUserToken } from '@/utile/stores.js'
const baseURL = 'http://big-event-vue-api-t.itheima.net'

const instance = axios.create({
  baseURL,
  timeout: 10000
})

// 添加请求拦截器
instance.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  config.headers.Authorization = store.state.user.token
  return config
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error)
})

// 添加响应拦截器
instance.interceptors.response.use(function (response) {
  // 2xx 范围内的状态码都会触发该函数。
  // 对响应数据做点什么
  if (response.data.code === 0) {
    return response.data
  }

  Message.error(response.data.message || '服务异常')
  return response.data
}, async function (error) {
  // 超出 2xx 范围的状态码都会触发该函数。
  // 对响应错误做点什么
  // if (error.response?.status === 400) {
  //   const data = await studentRegister()
  //   console.log(data)
  // }

  if (error.response?.status === 401) {
    router.push('/login')
    removeUserToken()
  }
  Message.error(error.response.data.message || '服务器异常')
  return Promise.reject(error)
})

export default instance
export { baseURL }
