// 商品管理信息
import $http from '@/utile/$goods.js'

// 登录接口
export const guserLogin = () => $http.post('/passport/login', {
  form: {
    isParty: false,
    mobile: '13324820302',
    smsCode: '246810',
    partyData: {}
  }
})

// 获取商品列表
export const getGoodsList = () => $http.get('/cart/list')

// 更新商品数量接口
export const updatedGoodsNum = (goodsId, goodsNum, goodsSkuId) => $http.post('/cart/update', {
  goodsId,
  goodsNum,
  goodsSkuId
})

// 删除商品接口
export const deleteGoods = (cartIds) => $http.post('/cart/clear', {
  cartIds
})
